.container {
    width: 1110px;
    height: 80%;
    display: flex;
    justify-content: space-between;
    border: 2px solid;
}

/*1111111111111111111111111111111111111111111111111111111111111111111111111*/
.firstBlock {
    background-color: #2f792f;
    height: 100%;
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.width100 {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.divPhoto {
    width: 100%;
    height: 180px;
    background-color: #f0f0f0;
}

.photo {
    margin-top: -90px;
    height: 180px;
    width: 180px;
    border-radius: 90%;
}

.inputFile {
    display: none;
}

.customfileupload {
    margin: 20px;
    border: 1px solid #ccc;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
    color: #000000;
}

.info {
    height: 40%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.infoItem {
    border-top: 1px solid gainsboro;

    padding: 20px;
}

/*222222222222222222222222222222222222222222222222222222222222222222222222222*/
.secondBlock {
    background-color: #792f43;
    height: 100%;
    width: 30%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.fullName{

}

.links {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.title{

}

.contacts {
    height: 90%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.titleContact{

}
.userInfoBlock{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.oneInfoContainer{
    border-top: 1px solid gainsboro;

    padding: 10px 20px 10px 20px;
}

/*33333333333333333333333333333333333333333333333333333333333333333333333333333*/
.endBlock {
    background-color: #2f3b79;
    height: 100%;
    width: 30%;
}
